.element {
    display: flex;
    position: relative;
    width: 282px;
    justify-self: center;
    align-items: center;
    flex-direction: column;
    margin: 0;
    padding: 0;
    z-index: 0;
    background-color: white;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 10px;
}